import React, { useEffect, useRef, useState, useMemo } from 'react';
import styled, { css, keyframes } from 'styled-components';
import { useLocation, Link } from 'react-router-dom'
import { useWeb3React } from '@web3-react/core/core/core';
import { useTranslation } from 'contexts/Localization';
import {ButtonMenu, ButtonMenuItem, Button ,Flex,Text} from '@pancakeswap/uikit';
import ConnectWalletButton from 'components/ConnectWalletButton'
import Gun from 'gun';
import { ethers } from 'ethers';
import Gift from '../Vote2/Gift'
import { runningLightAnimation, hoverEffect,activeEffect,borderGlow,ChatBox,Input,Container,Title,TopListTitle } from './StyledComponents'
import NumberPurchaseABI from './NumberPurchaseABI.json';
import GiftABI from './GiftABI.json';
import contractABI from './contractABI.json';

// GUN Inicializálása
const gun = Gun({
    peers: ['https://gun-manhattan.herokuapp.com/gun'] // vagy a saját szervered URL-je
  }); 

const Profile = () => {
    const { account } = useWeb3React();
    const [messages, setMessages] = useState([]);
    const [newMessage, setNewMessage] = useState(''); 
    const [cooldown, setCooldown] = useState(0);
    const chatBoxRef = useRef(null);
    const location = useLocation()
    const [topList, setTopList] = useState([]);
    const [top10Addresses, setTop10Addresses] = useState([]);
    const [currentPage, setCurrentPage] = useState(1);
    const [names, setNames] = useState({});
    const [userNumbers, setUserNumbers] = useState({});
    const [isSearching, setIsSearching] = useState(false);
    const [searchQuery, setSearchQuery] = useState('');
    const [emojiPickerVisible, setEmojiPickerVisible] = useState(false);
    const [image, setImage] = useState(null); // Kép kezeléséhez
    const itemsPerPage = 25;
    const { t } = useTranslation()
    const contractAddress = '0x674e3eDc898Aa4b486Bd2E5aa2D1C8653677F471';
    const provider = useMemo(() => new ethers.providers.Web3Provider(window.ethereum), []);
    const contract = useMemo(() => new ethers.Contract(contractAddress, contractABI, provider), [provider]);
    const fileInputRef = useRef(null);
    const contractAddress2 = '0xBE8C7E554C89e064ED7229952036c3678cd10180';
    const contract2 = useMemo(() => new ethers.Contract(contractAddress2, NumberPurchaseABI, provider), [provider]);

    useEffect(() => {
      const fetchMessages = async () => {
        gun.get('messages').map().once((data, key) => {
          setMessages((messages) => {
            const updatedMessages = [...messages, { ...data, id: key }];
            calculateTopList(updatedMessages);
            return updatedMessages;
          });
        });
      };
  
      fetchMessages();
    }, []);
  
  useEffect(() => {
  if (chatBoxRef.current) {
    chatBoxRef.current.scrollTop = chatBoxRef.current.scrollHeight;
  }
}, [account,messages]); 

useEffect(() => {
  const fetchUserNumbers = async () => {
    const newNumbers = {};
    const promises = topList.map(async ([user]) => {
      try {
        const userPurchase = await contract2.userPurchases(user);
        const userNumber = userPurchase.number.toNumber();
        newNumbers[user] = userNumber;
      } catch (error) {
        console.error(`Error fetching number for ${user}:`, error);
        newNumbers[user] = null;
      }
    });
    await Promise.all(promises);
    setUserNumbers(newNumbers);
  };

  if (topList.length > 0) {
    fetchUserNumbers();
  }
}, [topList, contract2]);

    useEffect(() => {
      if (cooldown > 0) {
        const timer = setTimeout(() => setCooldown(cooldown - 1), 1000);
        return () => clearTimeout(timer);
      }
      return undefined;
    }, [cooldown]);
  
    useEffect(() => {
      if (!account) return;
  
      const fetchNames = async () => {
        const newNames = {};
        const namePromises = topList.map(async ([user]) => {
          try {
            const name = await contract.getName(user);
            if (name) {
              newNames[user] = name;
            }
          } catch (error) {
            console.error(`Error fetching name for ${user}:`, error);
          }
        });
        await Promise.all(namePromises);
        setNames(newNames);
      };
  
      fetchNames();
    }, [topList, account, contract]);
  
    const toggleSearchMode = () => {
      setIsSearching(!isSearching);
      setSearchQuery('');
    };
  
    const handleSearchInputChange = (e) => {
      setSearchQuery(e.target.value);
    };

    const handleImageChange = (e) => {
      if (e.target.files && e.target.files[0]) {
        const reader = new FileReader();
        reader.onload = (e) => {
          setImage(e.target.result);
        };
        reader.readAsDataURL(e.target.files[0]);
      }
    };

    const handleSend = async () => {
      if (cooldown > 0) return;
  
      if (!newMessage.trim() && !image) return;
  
      const message = {
        text: newMessage,
        sender: account,
        image, // Itt használjuk a shorthandot
        timestamp: new Date().toISOString(),
      };
  
      gun.get('messages').set(message);
  
      setNewMessage('');
      setImage(null); // Kép törlése küldés után
      setCooldown(30); // 30 seconds cooldown
    };
  
    const calculateTopList = (messages) => {
      const sevenDaysAgo = new Date();
      sevenDaysAgo.setDate(sevenDaysAgo.getDate() - 7);
    
    
      const userMessageCount = new Map();
    
      messages
        .filter((msg) => new Date(msg.timestamp) > sevenDaysAgo)
        .forEach((msg) => {
          if (msg.text && msg.text.trim() !== '') {
            userMessageCount.set(msg.sender, (userMessageCount.get(msg.sender) || 0) + 1);
          }
        });
    
      const sortedTopList = Array.from(userMessageCount.entries()).sort((a, b) => b[1] - a[1]);
      setTopList(sortedTopList);
    
      const top10 = sortedTopList.slice(0, 25).map(([user]) => user);
      setTop10Addresses(top10);
    };
  
    const handlePageChange = (page) => {
      setCurrentPage(page);
    };
  
    const getSenderName = (sender) => {
      if (!sender) {
        return 'Unknown';
      }
      
      if (sender === '0xEa9C8f2D9d91cF44206F0d869b9cc39897a13Ae1') {
        return 'Admin';
      }
      
      return names[sender] || `${sender.substring(0, 5)}...${sender.substring(sender.length - 5)}`;
    };
    
    const getSenderStyle = (sender) => {
        if (sender === '0xEa9C8f2D9d91cF44206F0d869b9cc39897a13Ae1') {
            return { color: 'gold' }; // Arany szín az Adminnak
        }
        const index = topList.findIndex(([user]) => user === sender);
        if (index >= 0) {
            if (index < 25) {
                return { color: '#ffdc73' }; // Világosabb arany szín a top 25 felhasználónak
            }
            if (index < 100) {
                return { color: '#E5E4E2' }; // Ezüst szín a top 100 felhasználónak
            }
            if (index < 250) {
                return { color: '#cd7f32' }; // Bronz szín a top 250 felhasználónak
            }
        }
        return { color: '#fff' }; // Fehér szín alapértelmezettként
    };
    
    const getSenderImage = (sender) => {
      const userNumber = userNumbers[sender];
      if (userNumber) {
        const formattedNumber = userNumber < 10 ? `0${userNumber}` : `${userNumber}`;
        return `images/pixel/${formattedNumber}.jpg`;
      }
      return 'images/pixel/200.png';
    };
    
  
    const formatTimestamp = (timestamp) => {
      const date = new Date(timestamp);
      const now = new Date();
      const diffInSeconds = Math.floor((Number(now) - Number(date)) / 1000);
  
      if (diffInSeconds < 60) {
        return `${diffInSeconds} seconds ago`;
      }
      if (diffInSeconds < 3600) {
        const minutes = Math.floor(diffInSeconds / 60);
        return `${minutes} minutes ago`;
      }
      if (diffInSeconds < 86400) {
        const hours = Math.floor(diffInSeconds / 3600);
        return `${hours} hours ago`;
      }
      const days = Math.floor(diffInSeconds / 86400);
      return `${days} days ago`;
    };
  
    const startIndex = (currentPage - 1) * itemsPerPage;
    const endIndex = startIndex + itemsPerPage;
    const currentItems = topList.slice(startIndex, endIndex);
  
    const totalPages = Math.ceil(topList.length / itemsPerPage);
  
    const getPageNumbers = () => {
      const pageNumbers = [];
      const maxPageNumbers = 3;
      const startPage = currentPage;
  
      for (let i = 0; i < maxPageNumbers && startPage + i <= totalPages; i++) {
        pageNumbers.push(startPage + i);
      }
  
      return pageNumbers;
    };
    const getActiveIndex = (pathname: string): number => {
      if (
        pathname.includes('/lockedstaking/all') ||
        pathname.includes('/lockedstaking/history')
      ) {
        return 1
      }
      return 0
    }
  
    const filteredTopList = topList.filter(([user]) => {
      if (!searchQuery) return true;
      const userName = getSenderName(user);
      return userName.toLowerCase().includes(searchQuery.toLowerCase()) || user.toLowerCase().includes(searchQuery.toLowerCase());
    });
  
    const filteredMessages = messages.filter((msg) =>
      msg.text && msg.text.toLowerCase().includes(searchQuery.toLowerCase())
    );
    const sevenDaysAgo = new Date();
    sevenDaysAgo.setDate(sevenDaysAgo.getDate() - 7);

    return (
      <Container>
    {account ? (
        <>
        <div style={styles.container}>
        <Title>VizslaChat</Title>
             <ButtonMenu activeIndex={getActiveIndex(location.pathname)}  scale="sm" variant="subtle">
        <ButtonMenuItem id="swap-nav-link"  to="/chat"  as={Link}>
          {t('Chat')}
        </ButtonMenuItem>
          <ButtonMenuItem id="swap-nav-link" to="/nameregistration"  as={Link} mt="4px">
            {t('Settings')}
          </ButtonMenuItem> 
      </ButtonMenu>
        <ChatBox ref={chatBoxRef}>
        {filteredMessages.length > 0 ? (
              filteredMessages
                .filter((msg) => new Date(msg.timestamp) > sevenDaysAgo)
                .map((msg) => (
                  <div key={msg.id} style={styles.messageContainer}>
                    <img src={getSenderImage(msg.sender)} alt="sender" style={styles.avatar} />
                    <div style={styles.message}>
                      <b style={getSenderStyle(msg.sender)}>{getSenderName(msg.sender)}:</b> {msg.text}
                      {msg.image && <img src={msg.image} alt="sent" style={styles.sentImage} />}
                      <span style={styles.timestamp}>{formatTimestamp(msg.timestamp)}</span>
                    </div>
                  </div>
                ))
            ) : (
              <div style={styles.message}>No messages yet.</div>
            )}
          </ChatBox>
  <div style={styles.inputRow}>
  <Flex alignItems="center" justifyContent="center">
  <Input
  type="text"
  value={isSearching ? searchQuery : newMessage}
  onChange={isSearching ? handleSearchInputChange : (e) => setNewMessage(e.target.value)}
  onKeyDown={(e) => {
    if (e.key === 'Enter' && !isSearching && cooldown === 0) {
      handleSend();
    }
  }}
  style={styles.input}
  placeholder={isSearching ? 'Search in chatbox...' : (cooldown > 0 ? `Cooldown: ${cooldown}s` : 'Type a message')}
  disabled={cooldown > 0 && !isSearching}
/>

    <Button onClick={handleSend} style={styles.sendButton} disabled={cooldown > 0}>Send</Button>
    <Button onClick={() => setEmojiPickerVisible(!emojiPickerVisible)} style={styles.emojiButton}>
      😊
    </Button>
    <Button onClick={toggleSearchMode} style={styles.searchButton}>
      {isSearching ? 'X' : '🔍'}
    </Button>
    <Button 
  onClick={() => fileInputRef.current.click()} // Trigger file input on Button click
  style={styles.fileButton} 
>
📤
</Button>
    </Flex>
  </div>
  <input
  type="file"
  accept="image/*"
  ref={fileInputRef} // Attach ref to the file input
  style={{ display: 'none' }} // Hide the file input
  onChange={handleImageChange} // Keep the existing onChange handler
/>

  {emojiPickerVisible && (
  <div style={styles.emojiPicker}>
    <span 
      role="button" 
      tabIndex={0} 
      onClick={() => { 
        setNewMessage(`${newMessage}😊`); 
        handleSend(); 
      }} 
      onKeyDown={(e) => { 
        if (e.key === 'Enter') {
          setNewMessage(`${newMessage}😊`); 
          handleSend();
        }
      }}
    >😊</span>
    <span 
      role="button" 
      tabIndex={0} 
      onClick={() => { 
        setNewMessage(`${newMessage}😂`); 
        handleSend(); 
      }} 
      onKeyDown={(e) => { 
        if (e.key === 'Enter') {
          setNewMessage(`${newMessage}😂`); 
          handleSend();
        }
      }}
    >😂</span>
    <span 
      role="button" 
      tabIndex={0} 
      onClick={() => { 
        setNewMessage(`${newMessage}😢`); 
        handleSend(); 
      }} 
      onKeyDown={(e) => { 
        if (e.key === 'Enter') {
          setNewMessage(`${newMessage}😢`); 
          handleSend();
        }
      }}
    >😢</span>
    <span 
      role="button" 
      tabIndex={0} 
      onClick={() => { 
        setNewMessage(`${newMessage}😍`); 
        handleSend(); 
      }} 
      onKeyDown={(e) => { 
        if (e.key === 'Enter') {
          setNewMessage(`${newMessage}😍`); 
          handleSend();
        }
      }}
    >😍</span>
    <span 
      role="button" 
      tabIndex={0} 
      onClick={() => { 
        setNewMessage(`${newMessage}😎`); 
        handleSend(); 
      }} 
      onKeyDown={(e) => { 
        if (e.key === 'Enter') {
          setNewMessage(`${newMessage}😎`); 
          handleSend();
        }
      }}
    >😎</span>
  </div>
)}

  {/* Ez a rész biztosítja, hogy a fájl kiválasztó külön sorba kerüljön */}


        </div>
        <Gift/>

        <div style={styles.topListBox}>
        <TopListTitle>Top List</TopListTitle>
          <Input
            type="text"
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            style={styles.input}
            placeholder="Search by name or address"
          />
          <ul style={styles.topList}>
            {filteredTopList.slice(startIndex, endIndex).map(([user, count], index) => (
              <li key={user} style={getSenderStyle(user)}>
                <img src={getSenderImage(user)} alt="position" style={styles.topListImage} />
                {startIndex + index + 1}. {getSenderName(user)}
                <br />
                {user}: {count} messages
              </li>
            ))}
          </ul>
          <div>
            {currentPage > 1 && (
              <Button onClick={() => handlePageChange(currentPage - 1)} style={styles.paginationButton}>
                Previous
              </Button>
            )}
            {getPageNumbers().map((pageNumber) => (
              <Button
                key={pageNumber}
                onClick={() => handlePageChange(pageNumber)}
                style={styles.paginationButton}
              >
                {pageNumber}
              </Button>
            ))}
            {currentPage < totalPages && (
              <Button onClick={() => handlePageChange(currentPage + 1)} style={styles.paginationButton}>
                Next
              </Button>
            )}
          </div>
        </div>
        </>
    ) : (
      <ConnectWalletButton />
    )}
      </Container>
    );
};

